.header {
  width: 100%;
  height: 100px;
  background-color: rgba(0, 0, 0, 0.35);

  // position: relative;
}

.header__container {
  display: none;

  @include screen-sm {
    display: block;
    position: absolute;
    top: 50px;
    margin-top: -13px;
    right: 5%;
    z-index: 50;
  }
}